  mounted() {
    setTimeout(() => (this.overlay = false), 3000);
  },
  components: {
    // SystemBar,
    FooTer,
    FulfillingBouncingCircleSpinner,
  },
  computed: {
    mobile() {
      return this.$vuetify.breakpoint.smAndDown;
    },
  },
  watch: {
    mobile(newValue) {
      if (!newValue) {
        this.hideDrawer();
      }
    },
  },
  methods: {
    hideDrawer() {
      this.drawer = false;
    },
    toTheTop() {
      this.$vuetify.goTo(0);
    },
  },
};
</script>

